import React, { createContext, useState, useEffect, useRef } from 'react';
import DateInfo from './DateInfo';
import GetInfo from './GetInfo';
import Loading from './Loading';
import LocationConverter from './LocationConverter';

export const WeatherContext = createContext();

export default function WeatherProvider({ children }) {
  let today = new Date();
  const todayDate = DateInfo(today);    //     

  const [info, setInfo] = useState({});   //    
  const [state, setState] = useState({});   // api    
  const [on, setOn] = useState(false);    // api    state
  const [ready, getReady] = useState(false);    // api    state
  const [number, setNumber] = useState(1);    // api 
  const [howPer, setHowPer] = useState(50);   //     state


  useEffect(() => {
    setOn(false)    
    GetInfo(todayDate, number,convertLoaction.x,convertLoaction.y)    //   ,  x,y  
      .then(res => {
        if (number < 4) {   // 3 
          setInfo({ ...info, [number]: res.response.body.items.item });
          setState({ ...info, [number]: res.response.header });
          setNumber(num => num + 1)
        }
        else {    // 3  
          setOn(true);
        }
      })
      .catch(error => {
        setNumber(1);
        console.log(error," ")})
  }, [number])

  let changeDate = useRef(new Date());    //    
  let date = useRef(DateInfo(changeDate.current));    //     
  const [num, setNum] = useState(1);    // api   state
  const [page, setPage] = useState(1);    // api   state
  const [TMN, setTMN] = useState({});   //   state
  const [TMX, setTMX] = useState({});   //   state
  const [rain, setRain] = useState({});   //   state

  let rainList = useRef([]);    //   


  useEffect(() => {
    getReady(false)
    if (on) {
      let TMNres = info[page].find((aa) => { return aa.fcstDate === date.current && aa.category === "TMN" })    //   TMN 
      let TMXres = info[page].find((aa) => { return aa.fcstDate === date.current && aa.category === "TMX" })    //   TMX 
      let rainres = info[page].filter((aa) => { return aa.fcstDate === date.current && aa.category === "POP" })   //     

      if (rainres[rainres.length - 1].fcstTime === "2300") {    //    fcstTime 23
        for (let key in rainres) {    //     
          rainList.current.push(rainres[key])
        }
        setRain({ ...rain, [num]: rainList.current })
        rainList.current = [];    //   
      }
      else {    // 23 
        for (let key in rainres) {
          rainList.current.push(rainres[key])   //  
        }
      }


      switch (false) {    //        true  .  !       
        case (!(TMXres) || !(TMNres)):   // TMX TMN    
          setTMN({ ...TMN, [num]: TMNres.fcstValue })   
          setTMX({ ...TMX, [num]: TMXres.fcstValue })
          if (num < 3) {    // 3   
            changeDate.current = new Date(changeDate.current.setDate(changeDate.current.getDate() + 1));    //     1 
            date.current = DateInfo(changeDate.current);
            setNum(num => num + 1)
          }
          else {
            getReady(true)    // num 3   
          }
          break;
        case (!(TMNres)):   // TMN  
          setTMN({ ...TMN, [num]: TMNres.fcstValue });
          setPage(num => num + 1);    // page 
          break;
        case (!(TMXres)):   // TMX  
          setTMN({ ...TMX, [num]: TMXres.fcstValue })
          setPage(num => num + 1)
          break;
        default:    //    
          if (page < 3) {
            setPage(num => num + 1);
          }
          break;
      }
    }
  }, [num, page, on])



  const [bgToggle, setbgToggle] = useState(true);   //  
  const [settingToggle, setSettingToggle] = useState(false);    //  
  const [changeWeather, setChangeWeather] = useState("default");    // 
  const [userAddress, setUserAddress] = useState(false);    //    
  const [userAddressList, setUserAddressList] = useState([    //       
    {
      id: 0,
      address: ", ",
      selected:true,    //    
      Ma:37.5620769169639,    // 
      La:126.984901336292,    // 
    }
  ])
  const [userLocation, setUserLoaction] = useState(null);   //    
  const [convertLoaction,setConvertLocation] = useState({   //   
    x:60,
    y:127
  });    

  useEffect(() => {
    if (userAddressList) {    // userAddressList   selected true   
      const searchList = userAddressList.find((aa) => aa.selected)
      const loaction = LocationConverter("toXY", searchList.Ma, searchList.La)    //     
      setConvertLocation(loaction);
    }
  }, [userAddressList])  

  function bgToggleBtn() { 
    setbgToggle((show) => !show)
  }
  function settingToggleBtn() {
    setSettingToggle((show) => !show)
  }
  function refreshBtn() {   //     
    setNumber(1);
  }

  return (
    <>
      {!(ready) && <Loading />}
      {ready && <WeatherContext.Provider
        value={{
          changeWeather,
          setChangeWeather,
          bgToggle,
          bgToggleBtn,
          settingToggle,
          settingToggleBtn,
          setInfo,
          info,
          state,
          TMN,
          TMX,
          rain,
          setRain,
          howPer,
          setHowPer,
          refreshBtn,
          userAddress,
          setUserAddress,
          userLocation,
          setUserLoaction,
          convertLoaction,
          userAddressList, 
          setUserAddressList
        }}>
        {children}
      </WeatherContext.Provider>}
    </>
  )
}
